<template>
  <div class="tab__inner">

    <van-tabs v-model="childActive"
              @click="switchover"
              animated>
      <van-tab title="流行">
        <goodsItems :goodsList="popList"></goodsItems>
      </van-tab>
      <van-tab title="新款">
        <goodsItems :goodsList="newList"></goodsItems>
      </van-tab>
      <van-tab title="精选">
        <goodsItems :goodsList="sellList"></goodsItems>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import goodsItems from './goodsItems.vue'
export default {
  name: 'tab',
  data () {
    return {
      childActive: 0,
      tabTop: false,
      type: ['pop', 'new', 'sell']
    }
  },
  props: {
    active: {
      type: Number,
      default () {
        return 0
      }
    },
    popList: {
      type: Array,
      default: () => []
    },
    newList: {
      type: Array,
      default () {
        return []
      }
    },
    sellList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  watch: {
    active: {
      handler () {
        this.childActive = this.active
      },
      immediate: true
    }
  },
  methods: {
    switchover (active) {
      this.$emit('update:active', this.childActive)
      this.$emit('update:currentType', this.type[active])
    }
  },
  components: {
    goodsItems
  }

}
</script>
<style lang="scss" >
.tab__inner {
  font-size: 0.3rem;
}
</style>
