<template>
  <div class="home">
    <div class="home__header">购物街</div>

    <div class="tab__top">
      <van-tabs class="tab__tabs"
                :style="{'visibility':showTop?'visible':'hidden'}"
                v-model="active"
                ref="tab"
                v-show="showTop"
                @click="switchover"
                animated>
        <van-tab title="流行">
          <div></div>
        </van-tab>
        <van-tab title="新款">
        </van-tab>
        <van-tab title="精选">
        </van-tab>
      </van-tabs>
    </div>
    <betterScroll ref="scroll"
                  class="content1"
                  :probeType="3"
                  @scroll="scroll"
                  @pullingUp="loadmore"
                  :pullUpLoad="true">
      <swiper :banner="banner"></swiper>
      <div class="recommend">
        <div class="recommend__item"
             v-for="(value,index) in recommend.list"
             :key="index">
          <a :href="value.link">
            <img :src="value.image"
                 :alt="value.title">
            <p>{{value.title}}</p>
          </a>
        </div>
      </div>
      <div class="weekhot">
        <a href="">
          <img src="@/assets/img/home/recommend_bg.jpg"
               alt="">
        </a>
      </div>
      <tab :popList="pageType.pop.popList"
           :newList="pageType.new.newList"
           :sellList="pageType.sell.sellList"
           :active.sync="active"
           :currentType.sync="currentType"></tab>
    </betterScroll>
    <div @click="jumpTop"
         v-show="showTop"
         class="jump__top">
      <van-icon name="back-top" />
    </div>
  </div>
</template>
<script>
import swiper from './childs/swiper.vue'
import tab from './childs/tab.vue'
import betterScroll from '@/components/betterScroll'
import { getHomeMultidata, getHomeGoods } from '../../http/home'
export default {
  name: 'home',
  data () {
    return {
      active: 0,
      showTop: false,
      banner: [],
      recommend: [],
      currentType: 'pop',
      pageType: {
        pop: {
          page: 1,
          popList: []
        },
        new: {
          page: 1,
          newList: []
        },
        sell: {
          page: 1,
          sellList: []
        }
      }
    }
  },
  components: {
    swiper,
    tab,
    betterScroll
  },
  methods: {
    switchover (actions) {
      const switch1 = ['pop', 'new', 'sell']
      this.currentType = switch1[actions]
    },
    scroll (position) {
      if (position.y < -1000) {
        // this.$refs.tab.resize()
        this.showTop = true
        console.log(1)
      }
      if (position.y > -1000) {
        this.showTop = false
      }
    },
    async loadmore () {
      await this.getHomeGoods(this.currentType, this.pageType[this.currentType].page)
      this.$refs.scroll.scroll.finishPullUp()
      this.$refs.scroll.scroll.refresh()
    },
    jumpTop () {
      this.$refs.scroll.scroll.scrollTo(0, 0, 900)
    },
    getHomeMultidata () {
      getHomeMultidata()
        .then(res => {
          console.log(res)
          this.banner = res.data.banner
          this.recommend = res.data.recommend
        })
    },
    getHomeGoods (type = 'pop', page = 1) {
      getHomeGoods(type, page)
        .then(res => {
          console.log(res)
          console.log(this.pageType[type][type + 'List'])
          this.pageType[type][type + 'List'] = this.pageType[type][type + 'List'].concat(res.data.list)
          this.pageType[this.currentType].page++
        })
    },
    getScrollData () {
      // const htmlTop = document.documentElement.scrollTop
      // this.htmlTop = htmlTop
      // const clientHeight = document.documentElement.clientHeight
      // const htmlContentHeight = document.documentElement.offsetHeight

      // const differenceValue = htmlContentHeight - (htmlTop + clientHeight)
      // if (differenceValue < 1) {
      //   this.getHomeGoods(this.currentType, this.pageType[this.currentType].page)
      // }
      // if (htmlTop > 1000) {
      //   this.showTop = true
      // }
      // if (htmlTop < 1000) {
      //   this.showTop = false
      // }
    }
  },

  created () {
    this.getHomeMultidata()
    this.getHomeGoods('pop')
    this.getHomeGoods('new')
    this.getHomeGoods('sell')
    this.$nextTick(() => {
      console.log(this.recommend, 1111111111)
    })
    window.addEventListener('scroll', this.getScrollData, false)
  }
}
</script>
<style lang="scss">
.tab__tabs {
  margin-top: -2px;
  visibility: hidden;
}
.van-tabs__line {
  transform: translateX(60px) translateX(-50%);
  transition-duration: 0.3s;
}
.tab__top {
  position: absolute;
  z-index: 1000;
  top: 1rem;
  width: 100vw;
}

.content1 {
  overflow: hidden;
  position: absolute;
  top: 1rem;
  bottom: 50px;
  left: 0;
  right: 0;
}
.home {
  position: relative;
  height: 100vh;
}
.home__header {
  height: 1rem;
  font-size: 0.35rem;
  line-height: 1rem;
  text-align: center;
  color: white;
  background-color: #ff8a9d;
}
.van-grid-item {
  border-radius: 50%;
}
.recommend {
  display: flex;
  height: 2.7rem;
  padding-top: 0.2rem;
  border-bottom: 10px solid #eee;
  text-align: center;
  background-color: white;
  &__item {
    width: 25%;
  }
  img {
    width: 80%;
    vertical-align: bottom;
  }
  p {
    margin-top: 0.3rem;
    font-size: 0.3rem;
  }
}
.weekhot {
  img {
    width: 100%;
  }
}

.jump__top {
  position: fixed;
  right: 0.3rem;
  bottom: 2rem;

  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  text-align: center;
  background-color: rgb(214, 212, 212);
  i {
    font-size: 0.7rem;
  }
}
</style>
